	module.exports = {"data":{"markdownRemark":{"html":"<div class=\"post-container\">\n<picture>\n<source media=\"(min-width: 620px)\" srcset=\"/static/7t.jpg\">\n<img class=\"pic\" src=\"/static/7m.jpg\" alt=\"12\">\n</picture>\n</div>\n<div class = \"int-container\">\n<h1>Сегодня мы поговорили с Дашей Марининой, фотомоделью, а также мамой прекрасного мальчика Марка, который в течение всей съемки заряжал нашу съемочную группу своим заразительным смехом.</h1>\n<p><strong>Mommys band - Даша, ты выглядишь очень юно! Наверняка, незнакомые люди думают, что Марк - твой брат?</strong></p>\n<p><strong>Даша</strong> - Многие так и думают, хотя мы с ним не очень-то и похожи:)</p>\n<picture>\n<source media=\"(min-width: 620px)\" srcset=\"/static/7t.jpg\">\n<img class=\"pic\" src=\"/static/7m.jpg\" alt=\"12\">\n</picture>\n<p><strong>Mommys band - К тебе сразу пришло понимание того, что ты теперь мама? У меня, к примеру, это произошло только через пару лет. До этого все было как в тумане.</strong></p>\n<p><strong>Даша</strong> - Я ощущаю себя мамой с того дня, как узнала, что беремена. В тумане было несколько месяцев после родов, когда я вообще ничего не осознавала от недосыпов и усталости. </p>\n<p><strong>Mommys band - Поменялась ли твоя жизнь после рождения сына?</strong> </p>\n<p><strong>Даша</strong> - Жизнь поменялась кардинально. Во-первых, пришло осознание ответственности, что теперь надо следить не только за собой, но и за сыном. Во-вторых, я стала женственней, сменила джинсы на платья, начала растить активно волосы и чаще уделять время всяким девичьим делам. Также поменялся мой режим дня, теперь я просыпаюсь очень рано, несмотря на то, во сколько я легла и ложилась ли вообще.</p>\n<p><strong>Mommys band - Как давно ты занимаешься моделингом? Трудно ли совмещать работу с материнством?</strong></p>\n<p><strong>Даша</strong> - Моделингом занимаюсь с двадцати лет. Если честно, очень сложно стало последние полгода, поскольку Марк стал очень активным и не сидит на одном месте, а еще взял привычку лезть в кадр и частенько капризничает. </p>\n<p><strong>Mommys band - Расскажи о своём питании и как ты держишь себя в форме?</strong></p>\n<p><strong>Даша</strong> - Питание мое изменилось после родов. Если раньше я могла позволить себе фастфуд, то после родов я отдаю предпочтение полезной пище. В привычку взяла обязательно плотно завтракать. Отказалась от сахара и газировок, стала чаще есть овощи и фрукты. А чтобы держать себя в форме, конечно же не забываю и про спорт. </p>\n<p><strong>Mommys band - Бывает ли у тебя, что опускаются руки и нет ни на что сил? Откуда ты черпаешь вдохновение и откуда берёшь силы на все?</strong></p>\n<p><strong>Даша</strong> - Конечно, такое бывает. Во-первых, помогает хороший сон. Если пропало вдохновение, я беру сына и отправляюсь гулять в центр города, там я заряжаюсь общей энергетикой. Еще меня вдохновляют творческие люди: модели, стилисты, визажисты и другие интересные личности. Наблюдая за их успехом, тоже появляется желание покорять вершины, чтобы потом мой малыш гордился мамой.</p>\n</div>","frontmatter":{"date":"July 12, 2017","path":"/dasha","title":"Съемка с Дашей Марининой"}}},"pathContext":{}}

